import React from 'react'
import {Affix, Anchor} from "antd";
import { parse } from 'node-html-parser';

const { Link } = Anchor;

const getTagLevel = t => ~~t.replace('h', '');

const build = (parent, list) => {
    if (!list.length) {
        return parent;
    }

    const el = list.shift();

    if (el.level > parent.level) {
        parent.children.push( build(el, list) );
    }

    if (el.level < parent.level) {
        return el;
    }

    return parent;
}

const TOC = ({ post }) => {
    console.clear();

    const { html } = post;
    const tags = parse(html);

    // console.log(tags.childNodes);

    const headerTags = ['h1', 'h2', 'h3'];

    let headers = tags.childNodes
        .filter((tag) => headerTags.includes(tag.rawTagName) && tag.id)
        .map(tag => ({ children: [], level: getTagLevel(tag.rawTagName), id: tag.id, text: tag.innerText }));

    // let root = { children: [], level: 0 };

    return (
        <Affix offsetTop={100} style={{paddingLeft: 40}} className={"right-side"} >
            <Anchor>
                { headers.map(({ id, text, level }) => (
                    <div key={id} style={{ paddingLeft: level * 20}}>
                        <Link href={`#${id}`} title={text}/>
                    </div>
                )) }
            </Anchor>
        </Affix>
    );
}



export default TOC